<template>
  <header class="editor-background-image">
    <div class="editor-page">
      <div class="prompt-container">
        <p v-if="dailyPrompt" v-html="dailyPrompt" class="formatted-prompt"></p>

        <div class="button-div">
          <button @click="fetchNewPrompt" class="refresh-button">
            <i class="fas fa-sync-alt"></i>
          </button>
          <!-- Toggle ChatComponent visibility -->
          <button @click="toggleChatComponent" class="icon-button">
            <i class="fa-solid fa-comment-dots"></i>
          </button>
          <!-- Toggle Custom Prompt Request section visibility -->
          <button @click="toggleCustomPromptSection" class="icon-button">
            <i class="fa-solid fa-file-circle-question"></i>
          </button>
        </div>

        <!-- Conditionally rendered custom prompt request section -->
        <div v-if="showCustomPromptSection" class="custom-prompt-container">
          <h3>カスタムプロンプトをリクエストする</h3>
          <form @submit.prevent="submitCustomPromptRequest">
            <input
              type="text"
              v-model="customPromptTopic"
              placeholder="プロンプトのトピックまたはテーマを入力してください"
              required
            />
          </form>
          <p v-if="customPromptResponse" v-html="customPromptResponse" class="formatted-prompt"></p>
        </div>

        <!-- Conditionally rendered ChatComponent -->
        <ChatComponent v-if="showChatComponent" />
      </div>

      <div class="editor-container">
        <TrumbowygEditor />
      </div>
    </div>
  </header>
</template>

<script>
import { ref, onMounted } from 'vue';
import axios from 'axios';
import ChatComponent from './ChatComponent.vue';
import TrumbowygEditor from './TrumbowygEditor.vue';
import { getDailyPrompt, requestCustomPrompt } from '../services/chatgptService';

export default {
  components: {
    ChatComponent,
    TrumbowygEditor,
  },
  setup() {
    const entryId = ref(null);
    const entryTitle = ref('');
    const entryContent = ref('');
    const dailyPrompt = ref('');
    const customPromptTopic = ref('');
    const showChatComponent = ref(false);
    const showCustomPromptSection = ref(false);

    const loadEntry = async () => {
      try {
        if (entryId.value) {
          const response = await axios.get(`http://192.168.11.11:3000/api/entries/${entryId.value}`);
          entryTitle.value = response.data.title || 'Untitled';
          entryContent.value = response.data.content || '';
        }
      } catch (error) {
        console.error('Error loading entry:', error);
      }
    };

    const fetchDailyPrompt = async () => {
      try {
        dailyPrompt.value = await getDailyPrompt();
      } catch (error) {
        console.error('Error fetching daily prompt:', error);
      }
    };

    const fetchNewPrompt = async () => {
      await fetchDailyPrompt();
    };

    const submitCustomPromptRequest = async () => {
      try {
        dailyPrompt.value = await requestCustomPrompt(customPromptTopic.value);
        customPromptTopic.value = ''; // Clear input field after request
      } catch (error) {
        console.error('Error requesting custom prompt:', error);
      }
    };

    const saveEntry = async () => {
      const content = ''; // Replace with actual content retrieval logic
      try {
        if (entryId.value) {
          await axios.put(`http://192.168.11.11:3000/api/entries/${entryId.value}`, 
            { title: entryTitle.value, content }
          );
        } else {
          const response = await axios.post('http://192.168.11.11:3000/api/entries', 
            { title: entryTitle.value, content }
          );
          entryId.value = response.data.id;
        }
      } catch (error) {
        console.error('Error saving entry:', error);
      }
    };

    const toggleChatComponent = () => {
      showChatComponent.value = !showChatComponent.value;
    };

    const toggleCustomPromptSection = () => {
      showCustomPromptSection.value = !showCustomPromptSection.value;
    };

    onMounted(async () => {
      await loadEntry();
      await fetchDailyPrompt();
    });

    return {
      entryId,
      entryTitle,
      entryContent,
      dailyPrompt,
      customPromptTopic,
      showChatComponent,
      showCustomPromptSection,
      fetchNewPrompt,
      submitCustomPromptRequest,
      saveEntry,
      toggleChatComponent,
      toggleCustomPromptSection,
    };
  }
};
</script>



<style scoped>
.button-div {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.icon-button,
.refresh-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 24px;
  color: #2B2D42;
  width: 60px;
}

.icon-button:hover,
.refresh-button:hover {
  color: #667761;
  background-color: #DCEAB2;
}

.prompt-container,
.custom-prompt-container {
  margin-top: 20px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
}

.prompt-container h3,
.custom-prompt-container h3 {
  margin-bottom: 10px;
  font-size: 1.2em;
}

.prompt-container form,
.custom-prompt-container form {
  display: flex;
  flex-direction: column;
}

.prompt-container input,
.custom-prompt-container input {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
}

.custom-prompt-container .request-button {
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
}

.custom-prompt-container .request-button:hover {
  background-color: #0056b3;
}

.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.messages .user {
  color: #AB2346;
  text-align: right; /* Align user messages to the right */
  margin-bottom: 10px;
}

.messages .bot {
  color: #0E273C;
  text-align: left; /* Align bot messages to the left */
  margin-bottom: 10px;
}

.formatted-prompt {
  text-align: left; /* Align text to the left */
}

.formatted-prompt ul {
  padding-left: 20px; /* Adjust list padding */
}

.formatted-prompt li {
  margin-bottom: 5px; /* Adjust list item spacing */
}

.editor-background-image {
  background-size: cover;
  position: absolute;
  top: -230px; /* Adjust based on how much you want to move the background image up */
  left: 0;
  width: 100%;
  height: calc(100vh + 100px); /* Adjust height as needed */
  background-size: cover;
  z-index: 1;
  background-image: url('@/assets/texture.png'); /* Path to your texture image */
  background-color: #f8f8f8; /* Light background color for contrast */
}

.editor-page {
  flex: 1;
