<template>
    <div id="login-page">
          <h3>ログイン</h3>
      
      <div class="form-container">
        <form @submit.prevent="login">
          <div>
            <label for="username">ユーザー名</label>
            <input type="text" v-model="username" required />
          </div>
          <div>
            <label for="password">パスワード</label>
            <input type="password" v-model="password" required />
          </div>
          <button class="btn" type="submit">ログイン</button>
        </form>
      </div>
      </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        username: '',
        password: ''
      };
    },
    methods: {
    // ☆
      async login() {
        try {
          const response = await axios.post('http://192.168.11.11:3000/api/login', {
            username: this.username,
            password: this.password 
          });
          localStorage.setItem('token', response.data.token);
          localStorage.setItem('username', this.username);
  
          // Notify that user info has been updated
          this.$emit('user-logged-in', this.username);
          this.$router.push('/');
        } catch (error) {
          console.error(error);
          alert('Login failed');
        }
      }
    }
  };
  </script>
  
  <style scoped>
  #login-page {
    font-family: Arial, sans-serif;
  }
  
  .background-image {
    background: url('@/assets/cover.jpg') no-repeat center center;
    background-size: cover;
    height: 200px; /* Adjust height as needed */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  .entries-content {
    text-align: center;
  }
  
  .form-container {
    margin: 20px auto;
    max-width: 400px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  form div {
    margin-bottom: 15px;
  }
  
  form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  form input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .btn {
    background-color: #007bff;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .btn:hover {
    background-color: #0056b3;
  }
  </style>
  