<template>
  <div id="app">
    <HeaderComponent :title="'My App'" headerClass="banner-image" />
    <main class="content">
      <router-view @user-logged-in="updateUsername" />
    </main>
  </div>
</template>

<script>
import axios from 'axios';
import HeaderComponent from './components/HeaderComponent.vue';

export default {
  components: {
    HeaderComponent
  },
  data() {
    return {
      username: ''
    };
  },
  provide() {
    return {
      getUsername: () => this.username,
      setUsername: (name) => this.username = name
    };
  },
  mounted() {
    const token = localStorage.getItem('token');
    const username = localStorage.getItem('username');
    if (token && username) {
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      axios.get('http://localhost:3000/api/verify-token')
        .then(() => {
          // Token is valid, update username
          this.updateUsername(username);
        })
        .catch(() => {
          // Token is invalid, handle logout
          localStorage.removeItem('token');
          localStorage.removeItem('username');
          this.$router.push('/login');
        });
    } else {
      this.$router.push('/login');
    }
  },
  methods: {
    updateUsername(username) {
      this.username = username;
    }
  }
};
</script>

