<template>
  <header class="background-image">
    <div class="entries-content">
      <h3>エントリーの一覧</h3>
      <div class="select-all-container">
        <button @click="toggleSelectAll" :class="{ 'active': selectAll }">
          <i :class="selectAll ? 'fa-solid fa-check-square' : 'fa-solid fa-square'"></i>
          <span>すべて選択</span>
        </button>
      </div>
      <div class="entries-container">
        <ul>
          <li v-for="entry in journalEntries" :key="entry.id">
            <input type="checkbox" :id="'checkbox-' + entry.id" :value="entry.id" v-model="selectedEntries" class="custom-checkbox">
            <label :for="'checkbox-' + entry.id"></label>
            <router-link :to="`/journal/${entry.id}`" class="entry-link">{{ entry.title }} - {{ entry.createdOn }}</router-link>
          </li>
        </ul>
      </div>
      <div class="delete-button-container">
        <button @click="deleteSelectedEntries" :disabled="selectedEntries.length === 0">
          <i class="fa-solid fa-trash-can"></i> 削除
        </button>
      </div>
    </div>
  </header>
</template>

<script>
import axios from 'axios';

export default {
  name: 'JournalEntries',
  data() {
    return {
      journalEntries: [],
      selectedEntries: [],
      selectAll: false, // Property to track the select-all state
    };
  },
  created() {
    this.loadEntries();
  },
  methods: {
    getAuthToken() {
      return localStorage.getItem('token');
    },
    async loadEntries() {
      try {
        const token = this.getAuthToken();
        const response = await axios.get('http://192.168.11.11:3000/api/entries', {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });
        this.journalEntries = response.data.map(entry => ({
          id: entry.id,
          title: entry.title,
          createdOn: new Date(entry.createdOn).toLocaleDateString()
        }));
        this.updateSelectedEntries(); // Update selected entries based on new list
      } catch (error) {
        console.error('Error loading entries:', error);
      }
    },
    toggleSelectAll() {
      if (this.selectAll) {
        this.selectedEntries = [];
        this.selectAll = false;
      } else {
        this.selectedEntries = this.journalEntries.map(entry => entry.id);
        this.selectAll = true;
      }
    },
    updateSelectedEntries() {
      if (this.selectedEntries.length === this.journalEntries.length) {
        this.selectAll = true;
      } else {
        this.selectAll = false;
      }
    },
    async deleteSelectedEntries() {
      if (this.selectedEntries.length === 0) return;

      try {
        const token = this.getAuthToken();
        await Promise.all(this.selectedEntries.map(id =>
          axios.delete(`http://192.168.11.11:3000/api/entries/${id}`, {
            headers: {
              Authorization: `Bearer ${token}`
            }
          })
        ));
        this.loadEntries(); // Refresh entries list
        this.selectedEntries = [];
        this.selectAll = false; // Reset select-all state
      } catch (error) {
        console.error('Error deleting entries:', error);
      }
    }
  }
};
</script>


<style scoped>

.background-image {
  position: absolute;
  top: -230px; /* Adjust based on how much you want to move the background image up */
  left: 0;
  width: 100%;
  height: calc(100vh + 100px); /* Adjust height as needed */
  background: url('@/assets/cover.jpg') no-repeat center center;
  background-size: cover;
  z-index: 1;
}

.entries-content {
  position: relative;
  z-index: 2;
  padding: 20px;
  padding-left: 20px;
  padding-right: 20px;
  color: #000;
  margin-top: 0;
  background: rgba(255, 255, 255, 0.8); /* Light washout effect */
  border: 1px solid #ddd; /* Light border */
}

button {
  margin-top: 20px; /* Adds space above the button */
  background-color: #dc3545; /* Bootstrap's red color for danger */
  color: #fff; /* White text color */
  border: none;
  border-radius: 5px;
  padding: 10px 20px; /* Adjust padding for better button size */
  cursor: pointer; /* Change cursor to pointer for better UX */
  font-size: 16px; /* Adjust font size */
  transition: background-color 0.3s, transform 0.2s; /* Smooth transitions */
}

button:disabled {
  background-color: #e0e0e0; /* Grey color for disabled state */
  color: #6c757d; /* Darker text color for disabled state */
  cursor: default;
}

button:hover {
  background-color: #c82333; /* Darker red on hover */
}

button:active {
  transform: scale(0.98); /* Slightly shrink the button on click */
}

input[type="checkbox"] {
  display: none; /* Hide default checkbox */
}

.custom-checkbox + label {
  position: relative;
  display: inline-block;
  width: 20px; /* Adjust size of the custom checkbox */
  height: 20px; /* Adjust size of the custom checkbox */
  background: #ddd; /* Background color of unchecked checkbox */
  border-radius: 4px; /* Rounded corners */
  cursor: pointer;
  margin-right: 10px;
}

.custom-checkbox:checked + label {
  background: #c82333; /* Background color of checked checkbox */
}

.custom-checkbox:checked + label:after {
  content: "";
  position: absolute;
  left: 6px; /* Adjust based on design */
  top: 2px; /* Adjust based on design */
  width: 6px; /* Checkmark size */
  height: 12px; /* Checkmark size */
  border: solid white; /* Checkmark color */
  border-width: 0 2px 2px 0; /* Checkmark border style */
  transform: rotate(45deg); /* Rotate checkmark */
}

.select-all-container {
  margin-bottom: 10px; /* Space below the select-all button */
}

.select-all-container button {
  background: #938BA1; /* Primary color for the button */
  color: #fff; /* White text color */
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  font-size: 14px;
  display: flex;
  align-items: center;
}

.select-all-container button.active {
  background: #6c5b7b; /* Darker color when active */
}

.select-all-container button:hover {
  background: #6c5b7b; /* Darker color on hover (you can adjust this color) */
}

.select-all-container button i {
  margin-right: 5px; /* Space between icon and text */
}

.entries-container {
  max-height: 400px; /* Adjust this value based on your design */
  overflow-y: auto; /* Enable vertical scrolling */
  text-align: left;
}

.delete-button-container {
  text-align: left;
}

ul {
  list-style-type: none; /* Removes the bullets */
  padding: 0; /* Removes default padding */
  margin: 0; /* Removes default margin */
}

.entry-link {
  text-decoration: none; /* Removes the underline */
  color: inherit; /* Ensures the text color is consistent with the surrounding text */
}

.entry-link:hover {
  text-decoration: underline; /* Optional: Adds underline on hover for better UX */
}

  </style>