<template>
  <div class="editor-container">
    <input v-model="entryTitle" placeholder="タイトル" class="title-input" />
    <div :style="{ textAlign: 'left' }">
      <textarea id="editor" v-model="entryContent"></textarea>
    </div>
    <button @click="saveEntry" class="refresh-button">
      <i class="fa-solid fa-floppy-disk"></i>
    </button>
  </div>
</template>

<script>
import axios from 'axios';
import $ from 'jquery';
import 'trumbowyg/dist/trumbowyg.min';

export default {
  name: 'TrumbowygEditor',
  data() {
    return {
      entryId: null,
      entryTitle: '',
      entryContent: '',
    };
  },
  async mounted() {
    this.entryId = this.$route.params.id;
    if (this.entryId) {
      await this.loadEntry();
    }
    this.initializeEditor();
  },
  methods: {
    initializeEditor() {
      this.$nextTick(() => {
        $('#editor').trumbowyg();
        $('#editor').on('tbwchange', () => {
          this.entryContent = $('#editor').trumbowyg('html');
        });
      });
    },
    async loadEntry() {
      try {
        const response = await axios.get(`http://192.168.11.11:3000/api/entries/${this.entryId}`);
        this.entryTitle = response.data.title;
        this.entryContent = response.data.content;
        this.$nextTick(() => {
          $('#editor').trumbowyg('html', this.entryContent);
        });
      } catch (error) {
        console.error('Error loading entry:', error);
      }
    },
    async saveEntry() {
      const content = $('#editor').trumbowyg('html');
      try {
        if (this.entryId) {
          await axios.put(`http://192.168.11.11:3000/api/entries/${this.entryId}`, { title: this.entryTitle, content });
          this.$router.push(`/journal/${this.entryId}`);
        } else {
          const response = await axios.post('http://192.168.11.11:3000/api/entries', { title: this.entryTitle, content });
          const newEntryId = response.data.id;
          this.$router.push(`/journal/${newEntryId}`);
        }
      } catch (error) {
        console.error('Error saving entry:', error);
      }
    },
    setContent(content) {
      $('#editor').trumbowyg('html', content);
    },
    getContent() {
      return $('#editor').trumbowyg('html');
    },
  },
};
</script>


<style scoped>

.editor-container .trumbowyg-box .trumbowyg-editor {
  text-align: left !important; /* Force left alignment */
}

.editor-container {
  padding: 20px;
  background-color: #FFF8F0;
  flex: 1;
  margin-left: 10px;
}


.title-input {
  width: 300px;
  padding: 10px;
  font-size: 1.2em;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
}


.refresh-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 24px; /* Adjust icon size */
  color: #2B2D42; /* Icon color */
  width: 10%;
  margin-right: auto;
}


.refresh-button:hover {
  color: #667761; /* Icon color on hover */
  background-color: #DCEAB2;
}


textarea#editor {
  width: 100%;
  height: 300px;
  text-align: left; /* Align text to the left */

}


button {
  width: 300px;
  margin-top: 10px;
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
}


button:hover {
  background-color: #0056b3;
}


.save-button{
  background: none;
  border: none;
  cursor: pointer;
  font-size: 24px; /* Adjust icon size */
  color: #2B2D42; /* Icon color */
  width: 10%;
}




.save-button:hover {
  color: #667761; /* Icon color on hover */
  background-color: #DCEAB2;
}


</style>

