<!-- src/components/ChatComponent.vue -->
<template>
  <div class="chat-container">
    <div class="messages">
      <h3>チャットアシスタントと話す</h3>

      <div v-for="(message, index) in messages" :key="index" :class="message.type">
        {{ message.text }}
      </div>
    </div>
    <input v-model="inputMessage" @keyup.enter="sendMessage" placeholder="何かを聞いてください。" />
  </div>
</template>

<script>
import { sendMessage } from '../services/chatgptService';

export default {
  name: 'ChatComponent', // Updated name
  data() {
    return {
      inputMessage: '',
      messages: [],
    };
  },
  methods: {
    async sendMessage() {
      if (!this.inputMessage.trim()) return;

      // Add user message
      this.messages.push({ text: this.inputMessage, type: 'user' });

      // Clear input
      const messageToSend = this.inputMessage;
      this.inputMessage = '';

      try {
        // Send message to ChatGPT
        const response = await sendMessage(messageToSend);
        this.messages.push({ text: response, type: 'bot' });
      } catch (error) {
        this.messages.push({ text: 'Error: Unable to get response.', type: 'bot' });
      }
    },
  },
};
</script>

<style scoped>
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;

}

.messages .user {
  color: #AB2346;
  text-align: right; /* Align user messages to the right */
  margin-bottom: 10px;
}

.messages .bot {
  color: #0E273C;
  text-align: left; /* Align bot messages to the left */
  margin-bottom: 10px;
}

input {
  padding: 10px;
  border: none;
  border-top: 1px solid #ccc;
  border-radius: 5px; /* Rounded corners for the input box */
  font-size: 16px; 
}
</style>
