<template>
  <header class="journal-background-image">
    <div class="journal-page">
      <div class="prompt-container">
        <p v-if="dailyPrompt" v-html="dailyPrompt" class="formatted-prompt"></p>
        
        <div class="button-div">
          <button @click="fetchNewPrompt" class="refresh-button">
            <i class="fas fa-sync-alt"></i>
          </button>
          <!-- Toggle ChatComponent visibility -->
          <button @click="toggleChatComponent" class="icon-button">
            <i class="fa-solid fa-comment-dots"></i>
          </button>
          <!-- Toggle Custom Prompt Request section visibility -->
          <button @click="toggleCustomPromptSection" class="icon-button">
            <i class="fa-solid fa-file-circle-question"></i>
          </button>
        </div>

        <!-- Conditionally rendered custom prompt request section -->
        <div v-if="showCustomPromptSection" class="custom-prompt-container">
          <h3>カスタムプロンプトをリクエストする</h3>
          <form @submit.prevent="submitCustomPromptRequest">
            <input
              type="text"
              v-model="customPromptTopic"
              placeholder="プロンプトのトピックまたはテーマを入力してください"
              required
            />
          </form>
          <p v-if="customPromptResponse" v-html="customPromptResponse" class="formatted-prompt"></p>
        </div>

        <!-- Conditionally rendered ChatComponent -->
        <ChatComponent v-if="showChatComponent" />
      </div>

      <div class="editor-container">
        <TrumbowygEditor ref="editor" />
      </div>
    </div>
  </header>
</template>

<script>
import axios from 'axios';
import ChatComponent from './ChatComponent.vue';
import TrumbowygEditor from './TrumbowygEditor.vue';
import { getDailyPrompt, requestCustomPrompt } from '../services/chatgptService';

export default {
  components: {
    ChatComponent,
    TrumbowygEditor,
  },
  data() {
    return {
      entryId: this.$route.params.id,
      entryTitle: '',
      entryContent: '',
      dailyPrompt: '',
      isEditing: false, // Track editing state
      customPromptTopic: '',  // Add this
      customPromptResponse: '', // Add this
      showChatComponent: false, // For toggling ChatComponent visibility
      showCustomPromptSection: false,
    };
  },
  async mounted() {
    await this.loadEntry();
    await this.fetchDailyPrompt();
  },
  methods: {
    async loadEntry() {
      try {
        const response = await axios.get(`http://192.168.11.11:3000/api/entries/${this.entryId}`);
        this.entryTitle = response.data.title || 'Untitled';
        this.entryContent = response.data.content || '';
      } catch (error) {
        console.error('Error loading entry:', error);
      }
    },
    async fetchDailyPrompt() {
      try {
        this.dailyPrompt = await getDailyPrompt();
      } catch (error) {
        console.error('Error fetching daily prompt:', error);
      }
    },
    async fetchNewPrompt() {
      await this.fetchDailyPrompt();
    },
    async submitCustomPromptRequest() {
      try {
        this.dailyPrompt = await requestCustomPrompt(this.customPromptTopic);
        this.customPromptTopic = ''; // Clear input field after request
      } catch (error) {
        console.error('Error requesting custom prompt:', error);
      }
    },
    startEditing() {
      this.isEditing = true;
      this.$nextTick(() => {
        this.$refs.editor.setContent(this.entryContent); // Assuming TrumbowygEditor exposes setContent method
      });
    },
    async saveEntry() {
      const content = this.$refs.editor.getContent(); // Assuming TrumbowygEditor exposes getContent method
      try {
        if (this.entryId) {
          // Update existing entry
          await axios.put(`http://192.168.11.11:3000/api/entries/${this.entryId}`, { title: this.entryTitle, content });
        }
        // After saving, switch back to display mode
        this.isEditing = false;
      } catch (error) {
        console.error('Error saving entry:', error);
      }
    },
    toggleChatComponent() {
      this.showChatComponent = !this.showChatComponent;
    },
    toggleCustomPromptSection() {
      this.showCustomPromptSection = !this.showCustomPromptSection;
    },
  },
};
</script>


