<template>
    <header :class="headerClass">
        <h1>{{ title }}</h1>
        <slot name="extraContent"></slot>
        <div class="login-user" v-if="username">
            ログイン中 : {{ username }}
            <button class="btn" @click="logout">ログアウト</button>
        </div>
        <div class="login-user" v-else>
            ログインしてください
        </div>
    </header>
</template>

<script>
export default {
    name: 'HeaderComponent',
    props: {
        title: {
            type: String,
            default: 'FileManager'
        },
        headerClass: {
            type: String,
            default: 'default-header' // Corrected the class name here
        }
    },
    inject: ['getUsername', 'setUsername'],
    computed: {
        username() {
            return this.getUsername();
        }
    },
    methods: {
        logout() {
            localStorage.removeItem('token');
            localStorage.removeItem('username');
            this.setUsername('');
            this.$router.push('/login');
        }
    }
};
</script>

<style scoped>
.default-header {
    background-color: #f8f9fa;
    padding: 10px;
    text-align: center;
}
.custom-header {
    background-color: #343a40;
    color: #fff;
    padding: 10px;
    text-align: center;
}
</style>
